<template>
  <div class="collection">
    <hx-Return :title="title"></hx-Return>
    <div class="title">
      共
      <span>{{num}}</span>件商品
    </div>
    <!-- 收藏内容 -->
    <div class="order-content">
      <div class="item" v-for="(item,index) in list" :key="index">
        <div class="item-img">
          <img :src="item.url" @click="add(index)" alt />
        </div>
        <div
          class="item-tit van-multi-ellipsis--l2" @click="add(index)"
        ><img src="../assets/images/618.png"  alt="">{{item.title}}</div>
        <div class="item-price">
          <div class="item-pro">
            ￥
            <span>{{item.price}}</span>
          </div>
          <div class="count">
            <van-icon name="delete" @click="del(index)" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import hxReturn from "../components/hxReturn";

export default {
  data() {
    return {
      title: "我的收藏",
      num: 1,
      list: []
    };
  },
  methods:{
    del(index){
      this.$store.commit("Nolike",this.list[index].id)
      this.$store.commit("del")
      this.list.splice(index,1)
    },
    add(index){
      this.$router.push({ name: "productdetails", query: this.list[index] });
    }
  },
  created() {
    this.list = this.$store.state.likelist;
    // console.log(this.list)
    this.num = this.list.length
    //隐藏导航
    this.$store.state.vantTab = false;
  },
  // 切换组件生周期钩子，销毁函数
  // beforeDestroy() {
  //   this.$store.state.vantTab = true;
  // },

  components: {
    hxReturn
  }
};
</script>

<style lang="less" scoped>
.collection {
  width: 100%;
  height: calc(100% - 44px);
  padding-top: 44px;
  .title {
      height: 50px;
      line-height: 50px;
      padding-left: 10px;
      span{
          color: red;
          margin: 0 4px 0 4px;
      }
  }
  .order-content {
    .item {
      margin: 10px 0 10px 0;
      background-color: #ffffff;
      overflow: hidden;
      border-radius: 10px;
      div {
        float: left;
      }
      .item-img {
        width: 80px;
        height: 80px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .item-tit {
        height: 40px;
        margin-top: 10px;
        margin-left: 10px;
        width: calc(100% - 120px);
        font-size: 14px;
        img{
           width: 31px;
              height: 13px;
              margin-right: 10px;
        }
      }
      .item-price {
        margin-left: 10px;
        width: calc(100% - 120px);
        .item-pro {
          color: red;
        }
        .count {
          float: right;
          position: relative;
          top: 3px;
          font-size: 18px;
        }
      }
    }
  }
}
</style>